import axiosInstance from "./axiosInstance";

const getAllOrders = async () => {
  const response = await axiosInstance.get("/getOrderDetails");
  return response.data;
};

const getOrderById = async (orderId) => {
  const response = await axiosInstance.get(`/getOrderById/${orderId}`);
  return response.data;
};

const createOrder = (orderItems, address, pricingDetails, paymentMethod) => {
  return axiosInstance.post(
    "/placeOrder",
    {
      orderItem: orderItems,
      address,
      ...pricingDetails,
      payment_method: paymentMethod,
    },
    {
      headers: {
        "x-api-key": process.env.REACT_APP_ORDER_API_KEY,
      },
    }
  );
};

const editOrder = (
  orderId,
  address,
  pricingDetails,
  paymentMethod,
  hasPaymentCompleted,
  status
) => {
  return axiosInstance.post(
    `/editOrder/${orderId}`,
    {
      address,
      ...pricingDetails,
      payment_method: paymentMethod,
      payment_complete: hasPaymentCompleted,
      status: status,
    },
    {
      headers: {
        "x-api-key": process.env.REACT_APP_ORDER_API_KEY,
      },
    }
  );
};

const updateOrderStatus = (orderId, status) => {
  return axiosInstance.post(
    `/updateStatus/${orderId}`,
    { status },
    {
      headers: {
        "x-api-key": process.env.REACT_APP_ORDER_API_KEY,
      },
    }
  );
};

const getStatusHistory = async (orderId) => {
  const response = await axiosInstance.get(`/orderStatusDetails/${orderId}`);
  return response.data?.statusDetails?.orderStatusDetails;
};

const cancelOrder = async (orderId) => {
  const response = await axiosInstance.get(`/cancelDelivery/${orderId}`);
  return response.data;
};

const downloadInvoice = async (orderId) => {
  const response = await axiosInstance.get(`/downloadInvoice/${orderId}`);
  return response.data;
};

export {
  getAllOrders,
  getOrderById,
  createOrder,
  editOrder,
  updateOrderStatus,
  getStatusHistory,
  cancelOrder,
  downloadInvoice,
};
